* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary: #ffffff;
  --background: #030922;
  --dark-background: #02071b;
  --input-background: #091030;
  --roadmap-icon: #6b81d9;
  --text-blue: #2499f9;
  --blue: #1457ed;
  --green: #50fc61;
  --red: #ff3333;
  --subdue-button: #021338fa;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 0.5em;
  background-color: var(--background);
}

::-webkit-scrollbar-thumb {
  background-color: var(--blue);
}

/*::-webkit-scrollbar-track {
  /* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.75); 
}*/

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background);
  color: var(--primary);
  overflow-x: hidden;
}

nav,
section {
  margin: 5rem;
  margin-bottom: 8rem;
}

.blue {
  color: var(--blue);
}

.subdue {
  opacity: 0.7;
}

ul {
  list-style: none;
}

a {
  color: var(--primary);
  text-decoration: none;
}

.flex {
  display: flex;
}

.column {
  flex-direction: column;
}

.a-center {
  align-items: center;
}

.a-start {
  align-items: flex-start;
}

.j-center {
  justify-content: center;
}

.j-between {
  justify-content: space-between;
}

.gap {
  gap: 4rem;
}

.gap-half {
  gap: 0.5rem;
}

.gap-1 {
  gap: 1rem;
}

.gap-2 {
  gap: 2rem;
}

.text-center {
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 2rem;
}

p {
  font-size: 1.2rem;
  word-spacing: 0.2rem;
  line-height: 1.8rem;
}

.half-width {
  height: 80vh;
}

@media screen and (min-width: 280px) and (max-width: 1080px) {

  nav,
  section {
    margin: 2rem 1rem;
    margin-bottom: 2rem;
  }

  .flex {
    flex-direction: column;
  }

  .gap {
    gap: 1rem;
  }

  .gap-1 {
    gap: 0.5rem;
  }

  .gap-2 {
    gap: 0.5rem;
  }

  .half-width {
    width: 80vw;
    height: 100%;
  }

  h1 {
    font-size: 2rem;
  }

  p {
    font-size: 1.1rem;
    word-spacing: 0.2rem;
    line-height: 1.5rem;
  }
}
